'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { useCRMStore } from '@/hooks/useCRMStore';
import { Contact } from '@/lib/crmTypes';
import { ContactForm } from './ContactForm';
import { formatDate } from '@/lib/crmTypes';

export function ContactList() {
  const { state, deleteContact, searchContacts, getContactsByCompany, getContactsByTag } = useCRMStore();
  const [searchQuery, setSearchQuery] = useState('');
  const [groupBy, setGroupBy] = useState<'none' | 'company' | 'tag'>('none');
  const [selectedCompany, setSelectedCompany] = useState('');
  const [selectedTag, setSelectedTag] = useState('');

  // Get filtered contacts
  const getFilteredContacts = (): Contact[] => {
    let contacts = state.contacts;

    if (searchQuery) {
      contacts = searchContacts(searchQuery);
    }

    if (groupBy === 'company' && selectedCompany) {
      contacts = contacts.filter(contact => contact.company === selectedCompany);
    }

    if (groupBy === 'tag' && selectedTag) {
      contacts = contacts.filter(contact => contact.tags.includes(selectedTag));
    }

    return contacts;
  };

  // Get unique companies and tags for filtering
  const companies = [...new Set(state.contacts.map(contact => contact.company))].filter(Boolean);
  const allTags = [...new Set(state.contacts.flatMap(contact => contact.tags))].filter(Boolean);

  const filteredContacts = getFilteredContacts();

  // Group contacts if needed
  const groupedContacts = () => {
    if (groupBy === 'company') {
      const grouped = filteredContacts.reduce((acc, contact) => {
        const company = contact.company || 'Sin empresa';
        if (!acc[company]) acc[company] = [];
        acc[company].push(contact);
        return acc;
      }, {} as Record<string, Contact[]>);
      return grouped;
    }

    if (groupBy === 'tag') {
      const grouped: Record<string, Contact[]> = {};
      filteredContacts.forEach(contact => {
        if (contact.tags.length === 0) {
          if (!grouped['Sin etiquetas']) grouped['Sin etiquetas'] = [];
          grouped['Sin etiquetas'].push(contact);
        } else {
          contact.tags.forEach(tag => {
            if (!grouped[tag]) grouped[tag] = [];
            grouped[tag].push(contact);
          });
        }
      });
      return grouped;
    }

    return { 'Todos los contactos': filteredContacts };
  };

  const handleDeleteContact = (contactId: string) => {
    deleteContact(contactId);
  };

  const ContactCard = ({ contact }: { contact: Contact }) => (
    <Card className="hover:shadow-md transition-shadow">
      <CardContent className="p-4">
        <div className="flex justify-between items-start mb-3">
          <div>
            <h3 className="font-semibold text-lg text-slate-900">{contact.name}</h3>
            <p className="text-slate-600">{contact.company}</p>
          </div>
          <div className="flex space-x-2">
            <ContactForm
              contact={contact}
              trigger={
                <Button variant="outline" size="sm">
                  Editar
                </Button>
              }
            />
            <AlertDialog>
              <AlertDialogTrigger asChild>
                <Button variant="outline" size="sm" className="text-red-600 hover:text-red-700">
                  Eliminar
                </Button>
              </AlertDialogTrigger>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle>¿Eliminar contacto?</AlertDialogTitle>
                  <AlertDialogDescription>
                    Esta acción no se puede deshacer. Se eliminará permanentemente el contacto "{contact.name}".
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogCancel>Cancelar</AlertDialogCancel>
                  <AlertDialogAction
                    onClick={() => handleDeleteContact(contact.id)}
                    className="bg-red-600 hover:bg-red-700"
                  >
                    Eliminar
                  </AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>
          </div>
        </div>
        
        <div className="space-y-2 text-sm">
          <div className="flex items-center space-x-2">
            <span className="font-medium">Email:</span>
            <a href={`mailto:${contact.email}`} className="text-blue-600 hover:underline">
              {contact.email}
            </a>
          </div>
          <div className="flex items-center space-x-2">
            <span className="font-medium">Teléfono:</span>
            <a href={`tel:${contact.phone}`} className="text-blue-600 hover:underline">
              {contact.phone}
            </a>
          </div>
          {contact.notes && (
            <div className="flex items-start space-x-2">
              <span className="font-medium">Notas:</span>
              <p className="text-slate-600 flex-1">{contact.notes}</p>
            </div>
          )}
          {contact.tags.length > 0 && (
            <div className="flex flex-wrap gap-1 mt-2">
              {contact.tags.map((tag, index) => (
                <Badge key={index} variant="secondary" className="text-xs">
                  {tag}
                </Badge>
              ))}
            </div>
          )}
          <div className="text-xs text-slate-500 mt-2">
            Creado: {formatDate(contact.createdAt)}
          </div>
        </div>
      </CardContent>
    </Card>
  );

  const grouped = groupedContacts();

  return (
    <div className="space-y-6">
      {/* Header and Controls */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold text-slate-900">Contactos</h1>
          <p className="text-slate-600">Gestiona tus contactos y clientes</p>
        </div>
        <ContactForm
          trigger={
            <Button size="lg">
              Nuevo Contacto
            </Button>
          }
        />
      </div>

      {/* Search and Filter Controls */}
      <Card>
        <CardContent className="p-4">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="md:col-span-2">
              <Input
                placeholder="Buscar contactos..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            <Select value={groupBy} onValueChange={(value: 'none' | 'company' | 'tag') => setGroupBy(value)}>
              <SelectTrigger>
                <SelectValue placeholder="Agrupar por" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="none">Sin agrupar</SelectItem>
                <SelectItem value="company">Por empresa</SelectItem>
                <SelectItem value="tag">Por etiqueta</SelectItem>
              </SelectContent>
            </Select>
            {groupBy === 'company' && (
              <Select value={selectedCompany} onValueChange={setSelectedCompany}>
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar empresa" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="">Todas las empresas</SelectItem>
                  {companies.map(company => (
                    <SelectItem key={company} value={company}>{company}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            )}
            {groupBy === 'tag' && (
              <Select value={selectedTag} onValueChange={setSelectedTag}>
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar etiqueta" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="">Todas las etiquetas</SelectItem>
                  {allTags.map(tag => (
                    <SelectItem key={tag} value={tag}>{tag}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Results Summary */}
      <div className="flex justify-between items-center">
        <p className="text-slate-600">
          {filteredContacts.length} contacto{filteredContacts.length !== 1 ? 's' : ''} encontrado{filteredContacts.length !== 1 ? 's' : ''}
        </p>
      </div>

      {/* Contact Groups */}
      {Object.entries(grouped).map(([groupName, contacts]) => (
        <div key={groupName} className="space-y-4">
          {groupBy !== 'none' && (
            <h2 className="text-xl font-semibold text-slate-800 border-b border-slate-200 pb-2">
              {groupName} ({contacts.length})
            </h2>
          )}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {contacts.map(contact => (
              <ContactCard key={contact.id} contact={contact} />
            ))}
          </div>
        </div>
      ))}

      {/* Empty State */}
      {filteredContacts.length === 0 && (
        <Card>
          <CardContent className="p-8 text-center">
            <h3 className="text-lg font-semibold text-slate-900 mb-2">
              No se encontraron contactos
            </h3>
            <p className="text-slate-600 mb-4">
              {searchQuery || selectedCompany || selectedTag
                ? 'Intenta ajustar los filtros de búsqueda'
                : 'Comienza agregando tu primer contacto'}
            </p>
            {!searchQuery && !selectedCompany && !selectedTag && (
              <ContactForm
                trigger={
                  <Button>
                    Crear Primer Contacto
                  </Button>
                }
              />
            )}
          </CardContent>
        </Card>
      )}
    </div>
  );
}
